.content{
    padding: 10px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 15px;
}
.content__item{
    margin-top: 10px;
    font-family: "Roboto";
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.icons{
    margin-top: 10px;
    display: flex;
    justify-content: center;
}
.icons__item{
    margin: 10px;
}
.icon{
    transition: 0.3s;
    opacity: 0.7;
}
.icon:hover{
    opacity: 1;
    transform: scale(1.2);
}