.nav{
    padding: 30px;
    z-index: 10;
    position: fixed;
    top: 0; left: 0;
    max-width: 70vh;
    height: 100%;
    background: #101010;
    transition: 0.3s;
    transform: translateX(-999%);
    overflow-y: scroll;
    min-width: 100px;
    min-height: 100px;
}
.nav::-webkit-scrollbar{
    background: #101010;
    width: 7px;
}
.nav::-webkit-scrollbar-thumb{
    background: #aaa;
    border-radius: 5px;
}
.nav_active{
    transform: translateX(0)
}

.nav__item{
    margin-top: 20px;
}

.nav__h2{
    display: inline-block;
}

.nav__h2{
    color: #fff;
    font-family: Arial;
    font-size: 40px;
}
.nav__button{
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: 0.2s;
}
.nav__button:hover{
    opacity: 0.8;
}
.nav__close{
    position: absolute;
    color: #fff;
    right: 30px;
    font-family: Arial;
    font-size: 20px;
    cursor: pointer;
}