.header{
    position: relative;
    background: #151515;
    color: #fff;
    text-align: center;
    padding: 10px;
}
.header::before{
    content: "";
    position: absolute;
    bottom: -25px; left: 0;
    width: 100%;
    height: 50px;
    background: #151515;
    border-radius: 100%;
}
.img{
    border-radius: 100%;
    max-width: 200px;
    transform: scale(1.55);
    margin-bottom: 55px;
}
.h1{
    font-family: "IBM Plex Sans";
    margin-top: 10px;
}
.speciality{
    margin-bottom: 15px;
    color: #999999;
    font-family: Arial;
}
.burger{
    position: absolute;
    width: 35px;
    display: block;
    top: 15px; right: 15px;
    cursor: pointer;
}
.burger__item{
    width: 100%;
    height: 4px;
    background: #fff;
    margin-top: 7px;
    border-radius: 5px;
    transition: 0.2s;
}
.burger:hover .burger__item{
    background: rgb(255, 128, 0);
}