.nav_hide{
    height: 0;
}
.nav_show{
    height: 100%;
}
.nav_button_mt{
    margin-top: 15px;
}
.nav_rotate_icon{
    transform: rotate(180deg);
}

.nav__button{
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: 0.2s;
}
.nav__button:hover{
    opacity: 0.8;
}
.nav__img{
    width: 35px;
    transition: 0.3s;
}
.nav__text{
    font-size: 24px;
    color: #fff;
    font-family: "Open Sans";
}
.nav__item{
    margin-top: 20px;
}
.proj{
    overflow: hidden;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.col{
    flex: 1 500px;
    margin-top: 5px;
    position: relative;
}
.img{
    width: 100%;
    transition: 0.2s;
}
.about{
    opacity: 0;
    transition: 0.2s;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.8);
    color: #fff;
}
.about:hover{
    opacity: 1;
}

.h2, .aboutText{
    color: #fff;
    text-align: center;
}

.h2{
    font-family: "Montserrat";
    font-weight: 400;
}
.aboutText{
    font-family: "Open Sans";
}
.urls{
    position: absolute;
    bottom: 0; left: 0;
    width: 100%;
    display: flex;

}
.checkPage{
    flex: 5;
    display: block;
    background: #048cfc;
    color: #fff;
    padding: 5px;
    text-align: center;
    font-family: Arial;
    text-decoration: none;
}