.nav__item{
    margin-top: 20px;
}
.nav__list{
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
}
.nav_hide{
    height: 0;
}
.nav_show{
    height: 100%;
}
.nav_button_mt{
    margin-top: 15px;
}
.nav_rotate_icon{
    transform: rotate(180deg);
}
.skillBox{
    display: block;
    padding: 15px;
    background: #202020;
    display: flex;
    align-items: center;
    border-radius: 10px;
    margin: 5px;
    transition: 0.2s;
}
.skillBox:first-child{
    margin-left: 0;
}
.skillBox:hover{
    background: #151515;
}
.skillText{
    color: #fff;
    font-family: "Montserrat";
    font-size: 20px;
}
.skillImg{
    margin-right: 10px;
    width: 20px;
    height: 20px;
}
.nav__text{
    font-size: 24px;
    color: #fff;
    font-family: "Open Sans";
}
.nav__button{
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: 0.2s;
}
.nav__button:hover{
    opacity: 0.8;
}
.nav__img{
    width: 35px;
    transition: 0.3s;
}