.nav__item{
    margin-top: 20px;
}
.contact{
    overflow: hidden;
}
.nav_hide{
    height: 0;
}
.nav_show{
    height: 100%;
}
.nav_button_mt{
    margin-top: 15px;
}
.nav_rotate_icon{
    transform: rotate(180deg);
}
.nav__h3{
    color: #fff;
    font-family: Arial;
    font-size: 30px;
}
.nav__input{
    width: 100%;
    padding: 15px;
    background: #252525;
    font-family: "Nunito";
    font-size: 20px;
    letter-spacing: 0.3px;
    border-radius: 10px;
    border: 0;
    outline: 0;
    color: #fff;
    margin-top: 10px;   
}
.nav__input:focus{
    background: #202020;
}
.nav__textarea{
    min-height: 200px;
    max-height: 400px;
    resize: vertical;
}
.nav__textarea::-webkit-scrollbar{
    background: #101010;
    width: 7px;
}
.nav__textarea::-webkit-scrollbar-thumb{
    background: #aaa;
    border-radius: 5px;
}
.nav__button{
    cursor: pointer;
    display: flex;
    align-items: center;
    transition: 0.2s;
}
.nav__button:hover{
    opacity: 0.8;
}
.nav__img{
    width: 35px;
    transition: 0.3s;
}
.nav__text{
    font-size: 24px;
    color: #fff;
    font-family: "Open Sans";
}